	module.exports = {"data":{"post":{"html":"<p>Happy New Year from the three of us here in Lüllau!</p>\n<p>Mama's 1 year (actually 14 months) of maternity leave is now officially over. Today is a holiday; she starts work tomorrow. Task 1: getting to the office without a car, so she can drive her company car home.</p>\n<p>Actually, Task 0 (tomorrow and 3 days a week from now on): get Christopher to day care on time. And for PapaScott: get to work by 7:30, so that he can pick up Christopher from day care on time.</p>","fields":{"title":"Happy New Year","date":"01 Jan 2001","url":"https://papascott.de/2001-01-01-happy-new-year/"}}},"pathContext":{"slug":"/2001-01-01-happy-new-year/","prev":{"fields":{"title":"End of everything","date":"31 Dec 2000","slug":"/2000-12-31-end-of-everything/"}},"next":{"fields":{"title":"The adventure begins","date":"02 Jan 2001","slug":"/2001-01-02-the-adventure-begins/"}}}}

